.menu{
  padding-bottom: 14px;
  padding-top: 14px;
  border: 2px solid #adb5bd;
}
.menu li{
  width: 80px;
  text-align: center;
}


.banner{
  width: 100%;
  height: 480px;
  padding-left: 180px;
  background-image: url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/exhibition-week6/petri-heiskanen-bR9gFJqAnzA-unsplash.jpg) ;
  color: #fff;

}

.typeseting a{
  color:#fff;
  width: 160px;
  height: 44px;
  border: 1px solid #fff;
  text-align: center;
  line-height: 35px;
  border-radius: 5px;
  display: block;
  
}


.index img{
  width: 350px;
  height: 350px;
  object-fit: cover;
  flex:1;
}



.index h2{
  font-weight: bold;
}

.index h5{
  font-weight: bold;
}

.index p{
  font-weight: bold;
}

.live{
  width: 100%;
  height: 482px;
  background-image: url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/exhibition-week6/people2.png) ;
}

.bgc{
  width: 445px;
  height: 482px;
  background-color: #190014CC;
  padding-left: 24px;
  padding-top: 40px;
  color: #fff;
  
}

.bgc img{
  width: 128px;
  height: 128px;
}

.bgc p{
  font-size: 20px;
  color: #fff;
}

.bgc a{
  background-color: #E31277;
  
  
}

@media(max-width:767px){
 .row{
   display: flex;
   flex-direction: column;
 }

 .index img{
  width: 345px;
  height: 345px;
  object-fit: cover;
  flex:1;
}
}